<template>
  <div id="header">
    <div id="wrap" :style="{backgroundImage: bgImageURL}">
      <div id="nav">
        <template v-for="item in nav">
          <router-link :to="item === 'Home' ? '/' : item" :class="{active: item === navName}">{{item}}</router-link>
        </template>
      </div>
    </div>
    <div id="notes">
      <span class="left">“</span>
      <template v-for="item in notes.split(',')">
        <p>{{item}}</p>
      </template>
      <span class="right">“</span>
    </div>
  </div>

</template>

<script>
  export default {
    name: 'Home',
    props: {
      navName: String,
      notes: String
    },
    data() {
      return {
        bgImageURL: undefined,
        nav: [
          'Home',
          'Blog',
          'Life',
          'Movie',
          'Destination',
          'Hehe',
          'Drawing',
        ]
      }
    },
    mounted() {
      this.bgImageURL = 'url("https://picsum.photos/g/' + window.innerWidth + '/' + 400 + '/?random")';
    }
  }
</script>

<style scoped>
  #wrap {
    height: 300px;
    background-position: center;
    background-size: cover;
  }

  #nav {
    height: 300px;
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    font-family: Roboto, Helvetica, Arial, sans-serif;
    background: rgba(0, 0, 0, .5);
  }

  #nav a {
    display: inline-block;
    flex-direction: column;
    text-align: center;
    font-size: 18px;
    color: #fff;
    text-decoration: none;
    margin: 10px;
  }

  #notes {
    font-family: Helvetica, Arial, sans-serif, 'Avenir';
    font-size: 14px;
    line-height: 3em;
    margin: 20px auto;
    padding: 20px 40px;
    position: relative;
    text-align: center;
    max-width: 520px;
  }

  #notes .left,
  #notes .right {
    font-size: 36px;
    font-weight: bold;
    display: inline-block;
    position: absolute;
  }

  #notes .left {
    top: 0;
    left: 20px;
  }

  #notes .right {
    bottom: 0;
    right: 20px;
    transform: rotate(180deg);
  }

  /* mobile*/
  @media (max-width: 600px) {
    #nav {
      flex-direction: column;
    }

    #nav a {
      padding: 0 10px;
    }

    #nav a.active {
      border-left: 4px solid #ccc;
      font-weight: bold;
    }
  }

  /* PC*/
  @media (min-width: 600px) {
    #nav {
      flex-direction: row;
    }

    #nav a {
      padding: 10px 0;
      border-bottom: 4px solid transparent;
    }

    #nav a.active {
      border-bottom: 4px solid #fff;
    }
  }
</style>



