<template>
  <div id="bg" :style="{backgroundImage: bgImageURL}">
    <div id="wrap">
      <template v-for="item in nav">
        <router-link :to="item === 'Home' ? '/' : item">{{item}}</router-link>
      </template>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'Home',
    data() {
      return {
        bgImageURL: undefined,
        nav: [
          'Home',
          'Blog',
          'Life',
          'Movie',
          'Destination',
          'Hehe',
          'Drawing',
        ]
      }
    },
    mounted() {
      this.bgImageURL = 'url("https://picsum.photos/g/' + window.innerWidth + '/' + window.innerHeight + '/?random")';
    }
  }
</script>

<style scoped>
  #bg {
    padding: 0;
    margin: auto;

    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: -1;

    background-position: center;
    background-size: cover;
  }

  #wrap {
    margin: 0;
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    font-family: Roboto, Helvetica, Arial, sans-serif;
    background: rgba(0, 0, 0, .25);
  }

  #wrap a {
    flex-direction: column;
    text-align: center;
    line-height: 3em;
    font-size: 18px;
    color: #fff;
    text-decoration: none;
    padding: 0 10px;
  }

  @media (max-width: 600px) {
    #wrap {
      flex-direction: column;
    }
  }

  @media (min-width: 600px) {
    #wrap a {
      padding: 0 10px 40px;
    }
  }
</style>



