<template>
  <div>
    <Header navName="Blog" notes="一直有写写东西的习惯,择一些能贱人的放在这边"></Header>
    <div id="markdown-body">
      <div class="blog-wrap">
        <div class="blog-title">HER</div>

        <div class="blog" v-html="blogHTML">

        </div>
        <div class="blog-time">2018.5.15</div>
      </div>
      <div class="blog-wrap">
        <div class="blog" v-html="blogHTML1">

        </div>
      </div>

    </div>

  </div>
</template>

<script>
  import Header from './Header.vue'
  import marked from 'marked'
  export default {
    name: 'Blogs',
    components: {Header},
    data() {
      return {
        blog: ["> 一个基于webpack，切图写静态页面的脚手架工具\n" +
        "\n" +
        "### why cutt\n" +
        "\n" +
        "* 有切图，写静态页面、多页面的需求，需要快速搭建项目\n" +
        "* 页面热更新，使用[scss](https://www.sass.hk/)，借助[webpack](https://webpack.js.org/)编译打包，且不需要繁琐的配置\n" +
        "\n" +
        "### 安装\n" +
        "\n" +
        "* npm\n" +
        "\n" +
        "```\n" +
        "// npm全局安装cutt\n" +
        "npm install cutt -g\n" +
        "	\n" +
        "// 使用cutt <project-name> 生成项目\n" +
        "cutt website\n" +
        "	\n" +
        "// 进入项目\n" +
        "cd website\n" +
        "	\n" +
        "// 安装依赖\n" +
        "npm install\n" +
        "	\n" +
        "// 开发\n" +
        "npm run dev\n" +
        "```\n" +
        "* git(可选)\n" +
        "\n" +
        "```\n" +
        "// 下拉项目\n" +
        "git clone https://github.com/jackshawn/cutt.git\n" +
        "	\n" +
        "// 进入项目\n" +
        "cd cutt\n" +
        "	\n" +
        "// 安装依赖\n" +
        "npm install\n" +
        "	\n" +
        "// 开发\n" +
        "npm run dev\n" +
        "```\n" +
        "\n" +
        "### 开发\n" +
        "\n" +
        "* 目录结构\n" +
        "\n" +
        "```\n" +
        "cutt\n" +
        "├── dist/                                        # 打包文件目录\n" +
        "├── src/                                         # 源文件目录\n" +
        "│   ├── common/                                  # 公共页面目录\n" +
        "│   │   ├── header.html                          # 公共的页头\n" +
        "│   │   └── footer.html                          # 公共的页脚\n" +
        "│   ├── img/                                     # 图片目录\n" +
        "│   │   ├── favicon.jpg\n" +
        "│   │   └── logo.jpg\n" +
        "│   ├── js/                                      # js目录\n" +
        "│   │   ├── index.js                             # index页面的入口文件\n" +
        "│   │   ├── about.js                             # about页面的入口文件\n" +
        "│   │   ├── common.js                            # 公共的js文件\n" +
        "│   │   └── utils.js                             # 公共工具函数\n" +
        "│   ├── scss/                                    # 样式目录\n" +
        "│   │   ├── index.scss                           # index页面样式\n" +
        "│   │   ├── about.scss                           # about页面的样式\n" +
        "│   │   ├── common.scss                          # 公共的样式\n" +
        "│   │   ├── reset.css                            # 浏览器样式重置文件\n" +
        "│   │   └── variable.scss                        # scss变量文件\n" +
        "│   ├── index.html                  \n" +
        "│   └── about.html\n" +
        "├── package.json\n" +
        "├── README.md                  \n" +
        "├── cutt.config.js                               # cutt配置文件\n" +
        "└── webpack.config.js                            # webpack配置文件\n" +
        "```\n" +
        "\n" +
        "* cutt.config.js\n" +
        "\n" +
        "```\n" +
        "var config = {\n" +
        "    pages: ['index', 'about'], // 必选, 页面名, 不需要后缀\n" +
        "    host: '0.0.0.0', // 可选, 开发地址, 默认localhost\n" +
        "    port: 8081 // 可选, 端口, 默认8080\n" +
        "};\n" +
        "\n" +
        "module.exports = config;\n" +
        "```\n" +
        "\n" +
        "* .html\n" +
        "\n" +
        "```\n" +
        "<!DOCTYPE html>\n" +
        "<html lang=\"zh\">\n" +
        "<head>\n" +
        "    <meta charset=\"UTF-8\">\n" +
        "    <title>cutt</title>\n" +
        "    <meta http-equiv=\"X-UA-Compatible\" content=\"ie=edge\">\n" +
        "    <meta name=\"keywords\" content=\"some,seo,keywords\">\n" +
        "    <meta name=\"description\" content=\"some seo description\">\n" +
        "    <link rel=\"shortcut icon\" href=\"img/favicon.jpg\">\n" +
        "</head>\n" +
        "<body>\n" +
        "    #include(\"./common/header.html\")\n" +
        "    <div class=\"main\">\n" +
        "        <p><img src=\"img/logo.jpg\" alt=\"\"></p>\n" +
        "        <a class=\"nav\" href=\"about.html\">nav to about.html</a>\n" +
        "        <p class=\"common-color\">colored from common.scss</p>\n" +
        "        <p class=\"color\">colored from index.scss</p>\n" +
        "    </div>\n" +
        "    #include(\"./common/footer.html\")\n" +
        "</body>\n" +
        "</html>\n" +
        "```\n" +
        "需要编写的静态页面, 可以使用`#include(\"./common/footer.html\")`引用复用的页面部分；\n" +
        "\n" +
        "* 添加页面\n" +
        "\n" +
        "	webpack处理多页面搞起来还是有点麻烦，当添加新页面时需要：\n" +
        "\n" +
        "1. 停止服务\n" +
        "2. 相应目录下新建文件：new.html, new.scss, new.js\n" +
        "3. `cutt.config.js`添加新页面名`pages: ['index', 'about', 'new']`\n" +
        "4. `npm run dev`重新启动服务\n" +
        "\n" +
        "### 打包\n" +
        "\n" +
        "```\n" +
        "npm run build\n" +
        "```\n" +
        "编译打包后的文件在`dist/`下，可以双击浏览器打开；\n" +
        "\n" +
        "### TODO\n" +
        "\n" +
        "* 支持less\n" +
        "* 考虑引入jQuery\n" +
        "",`记得微博上有个关于星座的博主

以前看到关于那些星座的微博我都是一脸鄙夷的

把话说的模棱两可，放在谁身上都行

那些相信的人都似不似傻

最近看到个关于星座的文章我竟然控制不住看了看

甚至有点觉得有些话说到我心坎里了

甚至还去搜搜了处女座与天枰座

唉

才不是什么巴纳姆效应

是她`],
        blogHTML: undefined,
        blogHTML1: undefined
      }
    },
    mounted() {
      this.blogHTML = marked(this.blog[1]);
      this.blogHTML1 = marked(this.blog[0])
    }
  }
</script>

<style>
  .blog-wrap {
    position: relative;
    padding: 10px 20px;
    margin-bottom: 20px;
    background: #fff;
  }
  .blog-wrap .blog-title{
    display: inline-block;
    font-size: 48px;
    font-weight: bold;

    color: #fff;

    position: absolute;
    top: -11px;
    left: -32px;
  }
  .blog-wrap .blog-time{
    display: inline-block;
    font-size: 48px;
    font-weight: bold;

    color: #f7f7f7;

    position: absolute;
    right: -11px;
    bottom: -32px;
  }

</style>



